* {
  box-sizing: border-box;
}

html {
  background: #eef0f5;
}

.App {
  height: 100%;
  padding: 10px 0 50px 0;
}

.body {
  font-family: 'Raleway', sans-serif;
  width: 95%;
  margin: auto;
  padding-bottom: 60px;
  background: #fff;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
}

header {
  overflow: auto;
  margin-bottom: 40px;
}

h1 {
  box-sizing: border-box;
  text-align: center;
  margin: 10px 10px;
}

.header-main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-right: 30px;
}

.titlenav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.nav {
  display: block;
  list-style-type: none;
  text-align: center;
}

.nav li {
  display: inline-block;
  margin: 5px 10px;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.sitetitle {
  margin: 20px 10px 0 20px;
  font-size: 20px;
  text-align: left;
}

.date {
  font-size: 50px;
}

.month {
  font-weight: bold;
}

.dateDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-order: 2;
          order: 2;
}

.dateDiv2 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-right: 30px;
}

.weekday {
  margin-right: 30px;
  font-size: 15px;
  font-weight: bold;
}

main {
  position: relative;
}

.title {
  position: absolute;
  top: -30px;
  left: 5%;
  font-size: 14px;
  color: lightgrey;
}

.task-counter {
  position: absolute;
  right: 7%;
  top: 15px;
  font-size: 14px;
  color: lightgrey;
}

.completed-counter {
  position: absolute;
  bottom: -40px;
  right: 6%;
  color: lightgrey;
  font-size: 14px;
}

.add-task {
  width: 50%;
  padding: 12px 0 12px 10px;
  outline: none;
  border: none;
  background: rgba(245, 245, 245, 0.5);
  transition: 0.3s background ease;
}
.add-task:focus {
  background: rgba(245, 245, 245, 0.99);
}

.addBtn {
  background: white;
  border: none;
  padding: 0 15px;
  outline: 0;
  cursor: pointer;
  background: rgba(245, 245, 245, 0.8);
}
.addBtn:hover {
  background: lightgreen;
  color: white;
}

ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.additem {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: left;
          justify-content: left;
  margin-left: 30px;
}

.todolist {
  list-style: none;
  padding: 0;
}

.holder {
  transition: 0.3s all ease;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  /* border-bottom: 1px solid lightgrey; */
  margin: 0 auto;
  transition: 0.2s all;
  padding: 25px 0;
  width: 100%;
}
.holder:hover {
  background: rgba(245, 245, 245, 0.4);
}

.holder:hover .btn {
  visibility: visible;
}

.task {
  margin-right: 20px;
  margin-left: 40px;
}

.task-title {
  margin-left: 6%;
}

.btn {
  margin: 2px 3px;
  color: lightgrey;
  visibility: hidden;
}

.removeBtn,
.checkBtn,
.editBtn {
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}

.removeBtn:hover i {
  color: red;
  cursor: pointer;
}

.checkBtn:hover i {
  color: lightgreen;
}

.removeBtn i,
.checkBtn i,
.editBtn i {
  pointer-events: none;
}

.buttons {
  display: inline-block;
  margin-right: 20px;
}

.button {
  background: coral;
  padding: 1em 2em;
  color: #fff;
  border: 0;
}
.button:hover {
  background: #333;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(0, 0, 0, 0.3);
}

.modal-content {
  background-color: #f4f4f4;
  margin: 20% auto;
  padding: 20px;
  width: 80%;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
}

.btnClose {
  display: block;
  margin: 5px auto;
  font-size: 20px;
  border: none;
  border-radius: 20px;
  background: #4649c3;
  color: white;
  padding: 8px 20px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
  transition: 0.3s all;
}

.btnClose:hover,
.btnClose:focus {
  background: #676ad7;
  cursor: pointer;
  text-decoration: none;
}

/* OutsideClick.js style to get around click on left right side */
.outsideclick {
  width: 100%;
}

/* About page */
.about--h2,
.about--p {
  margin-left: 30px;
}

/* Media Queries   */
@media screen and (min-width: 481px) {
  body {
    width: 75%;
    margin: auto;
  }
}

@media screen and (min-width: 768px) {
  body {
    margin: auto auto auto 4%;
  }

  .add-task {
    width: 60%;
  }

  .modal-content {
    width: 50%;
    margin: 15% auto;
  }
}

@media screen and (min-width: 1024px) {
  body {
    width: 60%;
    margin: auto auto auto 2%;
  }

  .modal-content {
    width: 40%;
    margin: 10% auto;
  }
}

